const svg = d3.select('svg')

const width = +svg.attr('width');
const height= +svg.attr('height');
const eyeSpacing= 100;
const eyeYOffset = -70;
const eyeRadius = 40; 
const eyebrowWidth = 50;
const eyebrowHeight = 20;
const eyebrowYOffset = -70;

const g = svg.append('g')
    .attr('transform', `translate(${width/2}, ${height/2})`);

const circle = g.append('circle')
    .attr('r', 200)
    .attr('fill', 'yellow')
    .attr('stroke', 'black');

const eyeg = g.append('g')
    .attr('transform', `translate(0, ${eyeYOffset})`)

const leftEeye = eyeg.append('circle')
    .attr('r', eyeRadius)
    .attr('cx', - eyeSpacing)

const rightEye = eyeg.append('circle')
    .attr('r', eyeRadius)
    .attr('cx', + eyeSpacing)

const eyeBrowsG = eyeg.append('g')
    .attr('transform', `translate(0, ${eyebrowYOffset})`);

eyeBrowsG
    .transition().duration(2000)
        .attr('transform', `translate(0, ${eyebrowYOffset - 30})`)
    .transition().duration(2000)
        .attr('transform', `translate(0, ${eyebrowYOffset})`)

const leftEyebrow = eyeBrowsG.append('rect')
    .attr('x', -eyeSpacing - eyebrowWidth/2)
    .attr('width', eyebrowWidth)
    .attr('height', eyebrowHeight);

const rightEyebrow = eyeBrowsG.append('rect')
    .attr('x', +eyeSpacing - eyebrowWidth/2)
    .attr('width', eyebrowWidth)
    .attr('height', eyebrowHeight)


const mouth = g.append('path')
    .attr('d', d3.arc()({
        innerRadius: 90,
        outerRadius: 100,
        startAngle: Math.PI/2,
        endAngle: Math.PI * 1.5
    }));

    